body {
    margin: 0;
    color: #9c9a9a;
    font-family: 'Montserrat', sans-serif;
}

.main {
    height: 100vh;
    background: rgb(236,233,230);
    background: linear-gradient(-45deg, rgba(255,255,255,1) 0%, rgba(236,233,230,1) 100%);
}

.container {
    text-align: center;
    overflow: hidden;
}

.date {
    font-size: .7rem;
    text-transform: uppercase;
    margin: 2rem 0;
    font-weight: 300;
}

h1 {
    margin: 0 0 .5rem 0;
    font-weight: 300;
    color: #404040;
}

h3 {
    margin: 0 0 8rem 0;
    font-size: 1rem;
    font-weight: 300;
}

p {
    font-weight: 300;
}

.icon__big {
    font-size: 7rem;
    text-shadow: 8px 8px 10px rgba(0,0,0,.2);
    margin-bottom: 1rem;
    color: #404040;
}

.temp {
    font-size: 1.75rem;
    font-weight: 300;
    color: #404040;
}

.feelsLike {
    margin-bottom: 7rem;
}

.day {
    margin-bottom: 2rem;
    padding: 0 1rem;
}

.active {
    color: #404040;
}

.container__hourly {
    display: flex;
    justify-content: center;
}

.hourly__hour {
    padding: 0 .75rem 0 .75rem;
    color: #404040;
}

.hourly__time {
    margin-top: 0;
}

.icon__small {
    font-size: 2rem;
    text-shadow: 3px 3px 6px rgba(0,0,0,.2);
    margin-bottom: 0;
}